.difficulty-selector{position:relative;display:inline-block;margin-bottom:20px}.difficulty-selector label{font-family:"Protest Revolution",sans-serif;width:100px;margin-right:10px;color:#fff;font-size:14px}.difficulty-selector select{font-family:"Protest Revolution",sans-serif;appearance:none;border:1px solid #ccc;width:100px;padding:5px;border-radius:5px;cursor:pointer;-webkit-appearance:none;-moz-appearance:none;background-color:#fff;outline:none}.difficulty-selector::after{content:"▼";position:absolute;top:68%;right:10px;pointer-events:none;font-size:12px;color:#333}